.services__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 48px;
    
}
.service {
    background: var(--color-bg-variant);
    border-radius: 0 0 32px 32px;
    border: 1px solid var(--color-primary);
    height:fit-content;
    transition: var(--transition);
}

.service:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.service__head {
    background: var(--color-primary);
    border-radius: 0 0 32px 32px;
    padding: 32px;
    box-shadow: 0 32px 16px rgba(0, 0, 0, 0.1);
}
.service__head h3 {
    color: var(--color-bg-variant);
    font-size: 16px;
    text-align: center;
}
.service__list {
    padding: 32px;
}
.service__list li {
    display: flex;
    gap: 16px;
    margin-bottom: 12px;
}
.service__list-icon {
    color: var(--color-primary);
    margin-top: 32px;
}
.service__list p {
    font-size: 12px;
}

/* ============ MEDIA (MWDIUM DEVICE)============== */

@media screen and (max-width:1024px) {
    .service__container {
        grid-template-columns: 1fr 1fr;
        gap: 32px;
    }
    .service {
        height: auto;
        margin: 32px auto 64px;
    }
    .about__content p {
        margin: 16px 0 24px;
    }
}


/* ============= MEDIA QUERIES (SMALL DEVICES) ============== */
@media screen and (max-width:600px) {
    .service__container {
        grid-template-columns: 1fr;
        gap: 24px;
    }
    
}