nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 12px 28px;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 32px;
    display: flex;
    gap: 12px;
    border-radius: 48px;
    backdrop-filter: blur(15px);
    
}
nav a {
    background: transparent;
    padding: 14px;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 19px;
}
nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}
nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}